.zolo-image-gallery,
.zolo-single-image {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-gap: var(--zolo-item-grid-gap);
}

.zolo-image-gallery {
    --zolo-item-grid-gap: 30px;
    --zolo-icon-padding: 20px;
    --zolo-icon-border-radius: 50%;
    --zolo-icon-svg-size: clamp(1rem, 2vw, 1.25rem);
    --zolo-icon-svg-color: #1c1b25;
    --zolo-title-text-color: #0a0a0c;
    --zolo-title-margin: clamp(1.25rem, 3vw, 1.875rem);
    --zolo-title-padding: 8px 20px;
    --zolo-lightbox-caption-margin: 0 0 20px;
    --zolo-lightbox-caption-color: #dadadb;

    .zolo-item {
        position: relative;
        outline: none;
        &:hover {
            .zolo-image-wrap {
                &::before {
                    opacity: 1;
                }
            }
            .zolo-icon {
                opacity: 1;
            }
            .zolo-title {
                opacity: 1;
                transform: translateY(0);
            }
        }
    }
    .zolo-image-wrap {
        position: relative;
        height: 100%;
        border-radius: var(--zolo-item-border-radius);
        overflow: hidden;
        box-sizing: border-box;
        &::before {
            position: absolute;
            content: '';
            opacity: 0;
            width: 100%;
            height: 100%;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            z-index: 99;
            transition: all 0.3s ease-in-out;
            background-color: rgba(10, 10, 25, 0.369);
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            z-index: 11;
            position: relative;
        }
    }
    .zolo-icon-wrap {
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 111;
        transform: translate(-50%, -50%);
        text-decoration: none;
    }
    .zolo-icon {
        display: block;
        opacity: 0;
        background-color: #fff;
        padding: var(--zolo-icon-padding);
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: var(--zolo-icon-border-radius);
        z-index: 1;
        position: relative;
        overflow: hidden;
        transition: all 0.3s ease-in-out;
        box-sizing: content-box;
        &:hover {
            background-color: var(--zoloblocks-brand-color);
            svg {
                fill: var(--zoloblocks-brand-button-color);
            }
        }
    }
    .zolo__display-icon {
        box-sizing: content-box;
        svg {
            display: block;
            width: var(--zolo-icon-svg-size);
            height: var(--zolo-icon-svg-size);
            fill: var(--zolo-icon-svg-color);
        }
    }
    .zolo-title {
        position: absolute;
        bottom: 0;
        left: 0;
        padding: var(--zolo-title-padding);
        color: var(--zolo-title-text-color);
        font-size: 14px;
        margin: var(--zolo-title-margin);
        background-color: #fff;
        z-index: 99;
        transition: all 0.3s ease-in-out;
        border-radius: var(--zoloblocks-button-border-radius);
    }
    .zolo-title {
        opacity: 0;
        transform: translateY(10px);
    }
}
.media-upload {
    --zolo-media-padding: 35px;
    --zolo-media-icon-margin: 0 0 0 15px;
    align-items: center;
    display: flex;
    justify-content: center;
    padding: var(--zolo-media-padding) !important;
    background-color: aliceblue;
    font-weight: 500;
    svg {
        margin: var(--zolo-media-icon-margin);
    }
}

@media (min-width: 768px) {
    .zolo-image-gallery {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (min-width: 1024px) {
    .zolo-image-gallery {
        grid-template-columns: repeat(3, 1fr);
    }
}

// lightbox css here
.zolo-lightbox-caption {
    margin: var(--zolo-lightbox-caption-margin);
    font-size: 20px;
    text-transform: capitalize;
    color: var(--zolo-lightbox-caption-color);
}

// Animation

.zolo-newspaper {
    animation: newspaper 0.5s;
}

@keyframes newspaper {
    from {
        opacity: 0;
        transform: scale(0) rotate(500deg);
    }
    to {
        opacity: 1;
        transform: scale(1) rotate(0deg);
    }
}

// .zolo-horizontal

.zolo-move-horizontal {
    animation: horizontal 0.3s;
}

@keyframes horizontal {
    from {
        opacity: 0;
        transform: translateX(-50px);
    }
    to {
        opacity: 1;
        transform: translateX(0);
    }
}

// .zolo-move-form-top

.zolo-move-form-top {
    animation: top 0.2s;
}

@keyframes top {
    from {
        opacity: 0;
        transform: translateY(-100px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

// .zolo-zoom-in

.zolo-zoom-in {
    animation: zoom-in 0.2s ease-in-out;
}

@keyframes zoom-in {
    from {
        opacity: 0;
        transform: scale(0.8);
    }
    to {
        opacity: 1;
        transform: scale(1);
    }
}
// .zolo-zoom-out

.zolo-zoom-out {
    animation: zoom-out 0.3s ease-in-out;
}

@keyframes zoom-out {
    from {
        opacity: 0;
        transform: scale(1.3);
    }
    to {
        opacity: 1;
        transform: scale(1);
    }
}

// .zolo-3d-unfold

.zolo-3d-unfold {
    animation: unfold 0.3s ease-in-out;
}

@keyframes unfold {
    from {
        opacity: 0;
        transform-style: preserve-3d;
        transform: rotateY(-60deg);
    }
    to {
        opacity: 1;
        transform: rotateY(0deg);
    }
}
